<template>
    <div>
        <!-- 구글로그인 : loginvue24 -->
        <!-- 로그인 버튼 -->
        <button v-if="!isLoggedIn" @click="login" class="google-button">
            <img src="@/assets/google-login.png" alt="Login with Google" class="google-button-img" />
        </button>
        <!-- 로그아웃 버튼 -->
        <button v-if="isLoggedIn" @click="logout" class="logout-button">Logout</button>

        <!-- 디버깅 정보 표시 -->
        <div v-if="debugInfo" class="debug-info">
            <h3>디버깅 정보:</h3>
            <p>클라이언트 ID: {{ debugInfo.clientId }}</p>
            <p>현재 도메인: {{ debugInfo.origin }}</p>
            <p>현재 URL: {{ debugInfo.currentUrl }}</p>
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';

const oauthClientId = process.env.VUE_APP_OAUTH_CLIENT;
const isLoggedIn = ref(false);
const debugInfo = ref(null);

onMounted(() => {
    // 디버깅 정보 설정
    debugInfo.value = {
        clientId: oauthClientId,
        origin: window.location.origin,
        currentUrl: window.location.href
    };

    // Google Identity Services 스크립트 로드
    if (!document.querySelector('script[src="https://accounts.google.com/gsi/client"]')) {
        const script = document.createElement('script');
        script.src = 'https://accounts.google.com/gsi/client';
        script.async = true;
        script.defer = true;
        document.head.appendChild(script);
    }
});

const login = () => {
    console.log('=== 구글 로그인 시도 ===');
    console.log('클라이언트 ID:', oauthClientId);
    console.log('현재 도메인:', window.location.origin);
    console.log('현재 URL:', window.location.href);

    if (!oauthClientId) {
        alert('OAuth 클라이언트 ID가 설정되지 않았습니다.');
        return;
    }

    // Google Identity Services가 로드될 때까지 대기
    const checkGoogleLoaded = () => {
        if (window.google && window.google.accounts && window.google.accounts.oauth2) {
            console.log('Google Identity Services 로드 완료');
            // 직접 OAuth 2.0 팝업 로그인 시도
            showOAuthLogin();
        } else {
            console.log('Google Identity Services 로드 대기 중...');
            setTimeout(checkGoogleLoaded, 100);
        }
    };

    checkGoogleLoaded();
};

const showOAuthLogin = () => {
    try {
        console.log('OAuth 2.0 로그인 시작');
        // OAuth 2.0 팝업 로그인
        const client = window.google.accounts.oauth2.initCodeClient({
            client_id: oauthClientId,
            scope: 'email profile openid',
            ux_mode: 'popup',
            callback: handleOAuthResponse
        });

        console.log('OAuth 클라이언트 초기화 완료');
        client.requestCode();
    } catch (error) {
        console.error('OAuth 로그인 오류:', error);
        alert('구글 로그인에 실패했습니다: ' + error.message);
    }
};

const handleOAuthResponse = (response) => {
    console.log('OAuth 응답:', response);
    if (response.code) {
        console.log('OAuth 코드 수신:', response.code);
        isLoggedIn.value = true;
        alert('로그인 성공!');
    } else if (response.error) {
        console.error('OAuth 오류:', response.error);
        console.error('오류 설명:', response.error_description);
        alert('로그인에 실패했습니다: ' + response.error + (response.error_description ? '\n' + response.error_description : ''));
    } else {
        console.error('알 수 없는 OAuth 응답:', response);
        alert('로그인 중 알 수 없는 오류가 발생했습니다.');
    }
};

const logout = () => {
    console.log('로그아웃');
    isLoggedIn.value = false;
    if (window.google && window.google.accounts) {
        window.google.accounts.id.disableAutoSelect();
    }
};
</script>
<style scoped>
.google-button {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
}

.google-button-img {
    width: 200px; /* 버튼 이미지 크기 */
    height: auto;
    display: block;
}

.google-button:hover {
    opacity: 0.9; /* 마우스 오버 시 효과 */
}

.logout-button {
    padding: 10px 20px;
    background-color: #4285f4;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
}

.logout-button:hover {
    background-color: #357ae8; /* 마우스 오버 시 색상 변경 */
}

.debug-info {
    margin-top: 20px;
    padding: 15px;
    background-color: #f5f5f5;
    border-radius: 5px;
    font-family: monospace;
}

.debug-info h3 {
    margin-top: 0;
    color: #333;
}

.debug-info p {
    margin: 5px 0;
    color: #666;
}
</style>
